import React from 'react';
import { Link } from 'react-router-dom';


const ArticlesList = ({articles}) => (
    <>
        {articles.map((article,key) => (
            <div key={key} className="container p-3">
                <div className="row">
                    <div className="col-sm">
                        <div className="card rounded">
                            <div className="card-body">
                                <h5 className="card-title mt-0">{article.title}</h5>
                                <p className="card-text">{article.content[0].substring(0,150)}...</p>
                                <Link  to={`/article/${article.name}`} className="btn btn-warning articlebtn">
                                    Go to article
                                </Link>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        ))}
    </>
);


export default ArticlesList;

