import React, { useState } from 'react';

const AddCommentForm = ({ articleName, setArticleInfo }) => {
    const [username, setUsername] = useState('');
    const [commentText, setCommentText] = useState('');

    const addComment = async () => {
        const result = await fetch(`/api/articles/${articleName}/add-comment`, {
            method: 'post',
            body: JSON.stringify({ username, text: commentText }),
            headers: {
                'Content-Type': 'application/json',
            }
        });
        const body = await result.json();
        setArticleInfo(body);
        setUsername('');
        setCommentText('');
    }

    return (
        <form className="comment-form">
            <div className="form-group col-12 form-field">
                <label>Name</label>
                <input type="text" className="form-control" value={username} onChange={(event) => setUsername(event.target.value)} />
            </div>
            <br/>
            <div className="form-group  col-12 form-field">
                <label>Comment</label>
                <textarea className="form-control" rows="3" value={commentText} onChange={(event) => setCommentText(event.target.value)}/>
            </div>
            <br/>
            <button className="btn btn-primary" onClick={() => addComment()}>Add Comment</button>
            <br/>
            <br />
            <br/>
        </form>
    );
}

export default AddCommentForm;